
















import { PropType } from 'vue'

export default {
  props: {
    image: {
      type: String as PropType<string>,
      default: ''
    },
    name: {
      type: String as PropType<string>,
      default: ''
    },
    description: {
      type: String as PropType<string>,
      default: ''
    }
  },
  methods: {
    toggle() {
      const navEl = document.querySelector('.header-nav')
      navEl.classList.toggle('menu_active')
    }
  }
}
